import React, {Suspense, useState} from 'react'
import {Canvas} from '@react-three/fiber'
import {OrbitControls} from '@react-three/drei'
import Cat from "./models/Cat";
import {Plane, Vector3} from "three";
import Ball from "./models/Ball";
import BottomNavigation from "@mui/material/BottomNavigation";
import BottomNavigationAction from "@mui/material/BottomNavigationAction";
import AddBoxIcon from '@mui/icons-material/AddBox';
import ClearIcon from '@mui/icons-material/Clear';
import SportsBaseballIcon from '@mui/icons-material/SportsBaseball';
import Box from "./models/Box";
import ListItem from "@mui/material/ListItem";
import List from "@mui/material/List";
import IconButton from "@mui/material/IconButton";
import Grid from "@mui/material/Grid";
import ListItemText from "@mui/material/ListItemText";
import DeleteIcon from '@mui/icons-material/Delete';

export default function App() {

  const [isDragging, setIsDragging] = useState(false);
  const floorPlane = new Plane(new Vector3(0, 1, 0), 0);
  const [items, setItems] = useState([])
  const [naviValue, setNaviValue] = useState(null);
  let Accessories = ["Ball", "Box"]

  const appendBall = (index) => {
    setItems([...items, Accessories[index]]);
    console.log(items)
  }

  const clearBall = () => {
    setItems([])
  }

  const removeModel = (index) => {
    let array = [...items];
    array.splice(index, 1)
    setItems(array)
  }

  return (
      <>
        <Grid container style={{height: "90%"}}>
          <Grid item xs={12} style={{height: "70%"}}>
            <Canvas shadows>
              <Suspense fallback={null}>
                <ambientLight/>
                <Cat/>
                {
                  items.map((props, i) => {
                    if (props === "Ball") {
                      return <Ball setIsDragging={setIsDragging} floorPlane={floorPlane}/>
                    } else if (props === "Box") {
                      return <Box setIsDragging={setIsDragging} floorPlane={floorPlane}/>
                    }
                  })
                }
                <planeHelper args={[floorPlane, 5, "red"]}/>
                <OrbitControls
                    minAzimuthAngle={-Math.PI / 4}
                    maxAzimuthAngle={Math.PI / 4}
                    minPolarAngle={Math.PI / 6}
                    maxPolarAngle={Math.PI - Math.PI / 6}
                    minZoom={10} maxZoom={50} enabled={!isDragging}/>
              </Suspense>
            </Canvas>
          </Grid>
          <Grid item xs={12} style={{height: "30%", overflowY: 'scroll'}}>
            <List sx={{ bgcolor: 'background.paper'}}>
              {
                items.map((model, index) => {
                  return (
                      <ListItem
                          key={index}
                          disableGutters
                          secondaryAction={
                            <IconButton aria-label="comment">
                              <DeleteIcon onClick={() => removeModel(index)}/>
                            </IconButton>
                          }
                      >
                        <ListItemText primary={`${model}`}/>
                      </ListItem>
                  )
                })
              }
            </List>
          </Grid>
        </Grid>
        <BottomNavigation
            showLabels
            value={naviValue}
            onChange={(event, newValue) => {
              setNaviValue(newValue);
            }}
        >
          <BottomNavigationAction label="Add balls" color="danger" icon={
            <SportsBaseballIcon/>} onClick={() => appendBall(0)}/>
          <BottomNavigationAction label="Add boxes" color="success" icon={<AddBoxIcon/>} onClick={() => appendBall(1)}/>
          <BottomNavigationAction label="Clear balls" icon={<ClearIcon/>} onClick={clearBall}/>
        </BottomNavigation>
      </>
  )
}
