import React, {useRef, useState} from "react";
import Accessories from "./Accessories";

export default function Box({setIsDragging, floorPlane}) {
  const [hovered, hover] = useState(false)
  const dragObjectRef = useRef();

  return (
      <Accessories setIsDragging={setIsDragging} floorPlane={floorPlane}>
        <mesh
            rotation={[-Math.PI / 1.2, 0, 0]}
            onPointerOver={(event) => hover(true)}
            onPointerOut={(event) => hover(false)}
        >
          <boxGeometry args={[1, 1, 1]} ref={dragObjectRef}/>
          {hovered ? (<meshStandardMaterial color={'hotpink'} side={'double'}/>) : (
              <meshNormalMaterial attach="material"/>)}
        </mesh>
      </Accessories>
  );
}