import React, {useState} from "react";
import {useDrag} from "@use-gesture/react";
import {animated, useSpring} from "@react-spring/three";
import {useThree} from "@react-three/fiber";
import * as THREE from "three";

export default function Accessories(props) {
  const [pos, setPos] = useState([3, 1, 0]);
  let planeIntersectPoint = new THREE.Vector3();
  const [clicked, click] = useState(false)

  const [spring, api] = useSpring(() => ({
    position: pos,
    scale: 1,
    rotation: [0, 0, 0],
  }));

  const bind = useDrag(
      ({active, movement: [x, y], timeStamp, event}) => {
        if (active) {
          event.ray.intersectPlane(props.floorPlane, planeIntersectPoint);
          setPos([planeIntersectPoint.x, 1, planeIntersectPoint.z]);
        }

        props.setIsDragging(active);
        api.start({
          position: pos,
        });
        return timeStamp;
      },
      {delay: true}
  );

  return (
      <animated.mesh
          // onClick={(event) => click(!clicked)}
          {...spring} {...bind()} castShadow>
        {props.children}
      </animated.mesh>
  );
}